import React, { useEffect, useState } from "react";
import axios from "axios";
import {
  Container,
  Table,
  Button,
  Modal,
  Form,
  Row,
  Col,
  Spinner,
  Alert,
  Card, // (Import Card)
} from "react-bootstrap";

// BASE_URL (ID: 731547) และ Token ถูกต้อง
const BASE_URL = "https://api.baserow.io/api/database/rows/table/731547"; 
const API_TOKEN = "Token pJmbVWCV1NFQ7333Cmqj822hINsyguMW"; 

export default function Customers() {
  const [events, setEvents] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  const [show, setShow] = useState(false);
  const handleClose = () => setShow(false);
  const handleShow = () => setShow(true);

  // State ของฟอร์ม (ใช้ "เก็บใบเสร็จ")
  const [event, setEvent] = useState({
    id: 0,
    Name: "",
    Date: "",
    เก็บใบเสร็จ: "", 
  });

  const handleInputChange = (field, value) => {
    setEvent((prev) => ({ ...prev, [field]: value, }));
  };
  const resetEventForm = () => {
    setEvent({ id: 0, Name: "", Date: "", เก็บใบเสร็จ: "", });
  };

  // 3. ฟังก์ชัน List
  function listEvents() {
    setLoading(true);
    let config = { method: "get", url: `${BASE_URL}/?user_field_names=true`, headers: { Authorization: API_TOKEN }, };
    axios.request(config)
      .then((response) => { setEvents(response.data.results); setError(null); })
      .catch((error) => { console.log(error); setError("เกิดข้อผิดพลาดในการโหลดข้อมูลอีเว้น"); })
      .finally(() => { setLoading(false); });
  }

  // 4. vvv แก้ไขฟังก์ชัน Create vvv
  function createEvent() {
    if (event.Name.length <= 0) { alert("กรุณากรอกชื่องาน"); return false; }
    
    // แปลง YYYY-MM-DD เป็น ISO String
    const isoDate = event.Date ? new Date(event.Date).toISOString() : null;

    let data = {
      Name: event.Name,
      Date: isoDate, // <-- ใช้ค่าที่แปลงแล้ว
      เก็บใบเสร็จ: event.เก็บใบเสร็จ,
    };
    let config = { method: "post", url: `${BASE_URL}/?user_field_names=true`, headers: { Authorization: API_TOKEN }, data: data, };
    axios.request(config)
      .then(() => { 
        setShow(false); 
        listEvents(); 
        resetEventForm(); 
        setError(null); // (ล้าง Error เมื่อสำเร็จ)
      })
      .catch((error) => { console.log(error); setError("เกิดข้อผิดพลาดในการสร้างข้อมูล"); });
  }

  // 5. vvv แก้ไขฟังก์ชัน Update vvv
  function updateEvent(row_id) {
    // แปลง YYYY-MM-DD เป็น ISO String
    const isoDate = event.Date ? new Date(event.Date).toISOString() : null;

    let data = {
      Name: event.Name,
      Date: isoDate, // <-- ใช้ค่าที่แปลงแล้ว
      เก็บใบเสร็จ: event.เก็บใบเสร็จ,
    };
    let config = { method: "patch", url: `${BASE_URL}/${row_id}/?user_field_names=true`, headers: { Authorization: API_TOKEN }, data: data, };
    axios.request(config)
      .then(() => { 
        setShow(false); 
        listEvents(); 
        resetEventForm(); 
        setError(null); // (ล้าง Error เมื่อสำเร็จ)
      })
      .catch((error) => { console.log(error); setError("เกิดข้อผิดพลาดในการอัปเดตข้อมูล"); });
  }
  
  // 6. ฟังก์ชัน Delete
  function deleteEvent(row_id, name) {
    const confirmDelete = window.confirm(`คุณต้องการลบข้อมูลของ ${name} ใช่หรือไม่?`);
    if (!confirmDelete) return;
    let config = { method: "delete", url: `${BASE_URL}/${row_id}/`, headers: { Authorization: API_TOKEN }, };
    axios.request(config)
      .then(() => { listEvents(); })
      .catch((error) => { console.log(error); setError("เกิดข้อผิดพลาดในการลบข้อมูล"); });
  }

  // 7. vvv แก้ไขฟังก์ชัน Edit vvv
  const handleEdit = (obj) => {
    // แปลง ISO String (ที่ได้จาก Baserow) กลับเป็น YYYY-MM-DD
    const formattedDate = obj.Date ? obj.Date.split('T')[0] : "";

    setEvent({
      id: obj.id,
      Name: obj.Name || "",
      Date: formattedDate, // <-- ใช้ค่าที่แปลงแล้ว
      เก็บใบเสร็จ: obj.เก็บใบเสร็จ || "",
    });
    handleShow();
  };

  useEffect(() => { listEvents(); }, []);


  if (loading) {
    return <Spinner animation="border" role="status" className="mt-5" />;
  }

  return (
    // (ใช้ className ธีมสีอ่อนเหมือนเดิม)
    <Container className="p-4 light-page-container">
      <Row className="mb-3 align-items-center page-header-row">
        <Col xs={8}>
          <h1 className="mb-0">บริหารจัดการงานอีเว้น</h1>
        </Col>
        <Col xs={4} className="text-end">
          <Button
            variant="primary"
            onClick={() => [handleShow(), resetEventForm()]}
          >
            เพิ่มข้อมูลอีเว้น
          </Button>
        </Col>
      </Row>

      {/* (แสดง Error ถ้ามี) */}
      {error && <Alert variant="danger">{error}</Alert>}

      {/* (แสดงผลแบบ Card) */}
      <Row>
        {events.map((item) => (
          <Col sm={12} md={6} lg={4} key={item.id} className="mb-4">
            <Card className="h-100">
              <Card.Body className="d-flex flex-column">
                <Card.Title className="mb-3">{item.Name}</Card.Title>
                <Card.Text>
                  {/* vvv แปลง ISO Date กลับมาแสดงผล vvv */}
                  <strong>วันที่:</strong> {item.Date ? item.Date.split('T')[0] : 'N/A'}
                </Card.Text>

                <div className="mt-auto">
                  <hr /> 
                  <Button 
                    variant="outline-warning" 
                    href={item.เก็บใบเสร็จ} 
                    target="_blank"
                    className="w-100 mb-2"
                  >
                    เก็บใบเสร็จ
                  </Button>
                  
                  <div className="d-grid gap-2 d-sm-flex">
                    <Button 
                      variant="warning" 
                      onClick={() => handleEdit(item)}
                      className="flex-fill"
                    >
                      แก้ไข
                    </Button>
                    <Button 
                      variant="danger" 
                      onClick={() => deleteEvent(item.id, item.Name)}
                      className="flex-fill"
                    >
                      ลบ
                    </Button>
                  </div>
                </div>
              </Card.Body>
            </Card>
          </Col>
        ))}
      </Row>
      
      {/* (Modal) */}
      <Modal show={show} onHide={handleClose} className="light-modal">
        <Modal.Header closeButton>
          <Modal.Title>ฟอร์มข้อมูลอีเว้น</Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <Form.Group className="mb-3">
            <Form.Label>ชื่องาน</Form.Label>
            <Form.Control type="text" value={event.Name} onChange={(e) => handleInputChange("Name", e.target.value)} />
          </Form.Group>
          <Form.Group className="mb-3">
            <Form.Label>วันที่</Form.Label>
            <Form.Control type="date" value={event.Date} onChange={(e) => handleInputChange("Date", e.garget.value)} />
          </Form.Group>
          <Form.Group className="mb-3">
            <Form.Label>ลิงก์ใบเสร็จ (Google Drive)</Form.Label>
            <Form.Control type="text" placeholder="https://drive.google.com/..." value={event.เก็บใบเสร็จ} onChange={(e) => handleInputChange("เก็บใบเสร็จ", e.target.value)} />
          </Form.Group>
        </Modal.Body>
        <Modal.Footer>
          <Button variant="secondary" onClick={handleClose}>ปิด</Button>
          <Button variant="primary" onClick={() => event.id > 0 ? updateEvent(event.id) : createEvent()}>
            บันทึก
          </Button>
        </Modal.Footer>
      </Modal>
    </Container>
  );
}